      <hr>
      <p class="h3">商品清單</p>
      <div class="product p-3 bg-grey">
        <div class="row">
          <div class="col-lg-5 col-xs-12 product-info">
            <img
              :src="root + lessonCardBgPath"
              class="product-img">
            <p class="title mt-2 mb-0">{{ lessonName }}</p>
            <p
