                  </div>
                </div>
                <p class="discount text-danger">每位學生每門課限申請乙次</p>
              </div>
              <div
                v-if="lessonSaleUnit"
                :class="{'click':lessonSaleCase==='no_discount'}"
                class="program">
                <div
                  class="cursor"
                  @click="lessonSaleCase='no_discount';total=lessonFee">
                  <div class="program-name h3 m-0 text-left">
                    單節購買
                    <span class="h6">60分鐘</span>
                  </div>
                  <div class="program-time h3 m-0 text-right">
                    <span class="h6">NT$</span>
                    {{ lessonFee }}
                    <span class="h6">/小時</span>
                  </div>
                  <div class="program-price h3 m-0 text-right">
