                    {{ lessonFee }}
                  </div>
                </div>
                <p class="discount">原價小額購買</p>
              </div>
              <div
                v-for="(saleCase, c) in lessonSale"
                :key="'c'+c"
                :class="{'click':lessonSaleCase===c}"
                class="program">
                <div
                  class="cursor"
                  @click="lessonSaleCase=c;total=(saleCase.hour*Math.ceil(lessonFee*saleCase.off/100))">
                  <div class="program-name h3 m-0 text-left">
                    {{ saleCase.hour }}小時方案
                  </div>
                  <div class="program-time h3 m-0 text-right">
                    <span class="h6">NT$</span>
