                  </div>
                  <div class="program-price h3 m-0 text-right">
                    <span class="h6">總計NT$</span>
                    {{ saleCase.hour*Math.ceil(lessonFee*saleCase.off/100) }}
                  </div>
                </div>
                <p class="discount">{{ saleCase.off.slice(0,1) }}{{ (saleCase.off.slice(1,2)>0)?saleCase.off.slice(1,2):'' }}折｜共省下NT${{ saleCase.hour*(lessonFee-Math.ceil(lessonFee*saleCase.off/100)) }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <hr>
      <p class="h3">匯款資訊</p>
      <p class="content">
        金額：<span class="text-danger">NT$</span><span class="h3 text-danger">{{ total }}</span><br>
