        帳號：012345-411153<br>
        戶名：豆沙科技有限公司<br>
        <span class="tips">※完成匯款後，請務必於5天內，於本平台「帳務中心」頁面回填匯款末五碼，以利完成購買程序。</span><br>
        <span class="tips">※如發票有開立統編需求，請於匯款後5天內與我們聯絡</span><br>
        <span class="tips">※發票將依據國稅局公佈之統一發票中獎號碼自動進行對獎，若您幸運中獎屆時將會透過E-mail通知。</span>
      </p>
      <div class="row mt-5">
        <div class="col-xs-12 col-md-12 text-center">
          <v-btn
            class="mx-4 mb-4 p-4 h5 bg-button-color"
            @click="doSubmit()">
            <v-icon class="mr-2">mdi-shopping</v-icon>確認結帳
          </v-btn>
        </div>
        <!-- <pre style="word-wrap: break-word; white-space: pre-wrap;">{{ msg }}</pre> -->
      </div>
    </div>
    <v-snackbar
      v-model="snackbar"
      :top="true"
      :color="color">
      {{ errorMessages }}
      <v-btn
        dark
        flat
        @click="snackbar = false">
        Close
      </v-btn>
    </v-snackbar>
  </v-content>
