              <span class="h6 m-0 ml-1">({{ time.week }})</span>
            </div>
            <div class="d-block d-sm-none col-2 text-right p-0">
              <v-icon
                class="cursor"
                @click="doRemoveReservationTime(time)">mdi-close</v-icon>
            </div>
            <div class="col-12 col-sm-7 title">
              {{ time.timeStart}}-{{ time.timeEnd }}
              <v-chip class="m-0 ml-2 bg-main-color">{{ time.mins }}分鐘</v-chip>
            </div>
            <div class="d-none d-sm-block col-sm-1 text-right p-0">
