<script>
import qs from 'qs'
// import boyIcon from '@/assets/img/boy_icon.jpg'
// import girlIcon from '@/assets/img/girl_icon.jpg'
import cardBg1 from '@/assets/img/lesson_card_bg1.jpg'
import cardBg2 from '@/assets/img/lesson_card_bg2.jpg'
import cardBg3 from '@/assets/img/lesson_card_bg3.jpg'
import TimeSelector from '../Plugins/TimeSelector.vue'
import { mapState } from 'vuex'
export default {
  name: 'Reservation',
  components: {
    TimeSelector
  },
  metaInfo () {
    return {
      title: this.appName + '｜課程預約｜' + this.lessonName
    }
  },
  data: function () {
    return {
      pageView: false,
      // boyIcon,
      // girlIcon,
      cardBg1,
      cardBg2,
      cardBg3,
      snackbar: false,
      color: 'red',
      errorMessages: '',
      teacherId: -1,
      teacherNickname: '',
      lessonId: '',
      lessonCardBgPath: '',
      lessonName: '',
      canReservationTime: 0,
      trialReservationInfo: false,
      lessonDateDefault: [],
      lessonDateBooked: [],
      selectTimeList: [],
      selectTimeDetailList: [],
      removeReservationTime: {}
    }
  },
  computed: {
    ...mapState(['root', 'appName']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    },
    reservationTimeList () {
      let list = []
      let weekList = ['日', '一', '二', '三', '四', '五', '六']
      this.selectTimeDetailList.forEach((s) => {
        let tempCode = 0
        s.time.forEach((t) => {
          if (t.code === (tempCode + 1)) {
            list[list.length - 1].mins = (list[list.length - 1].mins + 30)
            list[list.length - 1].code.push(t.code)
            list[list.length - 1].timeEnd = t.timeEnd
          } else {
            list.push({ days: s.days, date: s.date, week: weekList[s.week], mins: 30, code: [t.code], timeStart: t.timeStart, timeEnd: t.timeEnd })
          }
          tempCode = t.code
        })
      })
      return list
    },
    selectReservationTime () {
      let min = 0
      this.reservationTimeList.forEach((r) => {
        min = min + r.mins
      })
      return min
    },
    lastReservationTime () {
      return this.canReservationTime - this.selectReservationTime
    }
  },
  watch: {
  },
  // Sends action to Vuex that will log you in and redirect to the dash otherwise, error
  mounted () {
    if (localStorage.getItem('token') && this.userInfo.type === 'student') {
      let body = {
        userId: this.userInfo ? this.userInfo.user_id : -1,
        url: this.$route.path
      }
      this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            if (res.data.authority) {
              let reservationInfo = JSON.parse(localStorage.getItem('reservation'))
              if (reservationInfo) {
                this.teacherId = reservationInfo ? reservationInfo.teacherID : ''
                this.lessonId = reservationInfo ? reservationInfo.lessonID : ''
                this.canReservationTime = reservationInfo ? reservationInfo.time : 0
                this.trialReservationInfo = reservationInfo ? reservationInfo.trial : false
                this.getLessonInfo()
                // this.getLessonSpecificAvailableTime()
              } else {
                this.snackbar = true
                this.color = 'red'
                this.errorMessages = '您尚未選擇課程'
                setTimeout(() => {
                  this.$router.push('/')
                }, 3000)
              }
            } else {
              // this.$router.push('/')
            }
          } else {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
    } else {
      this.$router.push('/')
    }
  },
  methods: {
    test () {
      alert('未開放')
    },
    // getTeacherProfile (id) {
    //   this.$http.get(`/api/account/returnTeacherProfileForPublicViewing/?userID=${id}&type=teacher`).then(res => res.data)
    //     .then(res => {
    //       if (res.status === 'success') {
    //         this.pageView = true
    //         this.teacherApproved = res.data.id_approved
    //         // this.teacherAvgScore = res.data.all_lesson_score_mean
    //         // this.teacherReviewedTimes = res.data.total_number_of_remark
    //         this.teacherEdu1 = res.data.education_1
    //         this.teacherEdu2 = res.data.education_2
    //         this.teacherEdu3 = res.data.education_3
    //         this.teacherEduApproved = res.data.education_approved
    //         this.teacherJob = res.data.company
    //         this.teacherJobApproved = res.data.work_approved
    //         this.teacherExp = res.data.special_exp
    //         this.teacherExpApproved = res.data.other_approved
    //         this.teacherAllApproved = res.data.is_approved
    //         this.teacherNickname = res.data.nickname
    //         this.teacherYear = res.data.tutor_experience
    //         // this.teacherIntro = res.data.intro
    //         // this.teacherTag = res.data.subject_type
    //         this.teacherGender = res.data.is_male ? boyIcon : girlIcon
    //         if (res.data.thumbnail_dir !== '') {
    //           this.teacherPhoto = res.data.thumbnail_dir
    //         } else {
    //           if (res.data.is_male) {
    //             this.teacherPhoto = this.boyTeacher
    //           } else {
    //             this.teacherPhoto = this.girlTeacher
    //           }
    //         }
    //         this.lessonDateDefault = res.data.available_time || []
    //         this.lessonDateBooked = res.data.booked_time || []
    //       } else {
    //         this.$router.go(-1)
    //       }
    //     })
    // },
    getLessonInfo () {
      this.$http.get(`/api/lesson/returnLessonDetailsForBrowsing/?action=browsing&lessonID=${this.lessonId}&userID=${this.userInfo ? this.userInfo.user_id : -1}`).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            // this.pageView = true
            this.lessonCardBg = res.data.background_picture_code
            if (res.data.background_picture_code === 99) {
              this.lessonCardBgPath = res.data.background_picture_path
            } else if (res.data.background_picture_code === 0) {
              this.lessonCardBgPath = this.cardBg1
            } else if (res.data.background_picture_code === 1) {
              this.lessonCardBgPath = this.cardBg2
            } else if (res.data.background_picture_code === 2) {
              this.lessonCardBgPath = this.cardBg3
            }
            this.teacherId = res.data.teacher_auth_id
            this.teacherNickname = res.data.teacher_nickname
            this.lessonName = res.data.lesson_title // 課程名稱
            this.getLessonSpecificAvailableTime()
          } else {
            // this.snackbar = true
            // this.color = 'red'
            // this.errorMessages = res.errMsg
            this.$router.go(-1)
          }
        })
    },
    getLessonSpecificAvailableTime () {
      let body = {
        userID: this.userInfo ? this.userInfo.user_id : -1,
        lessonID: this.lessonId
      }
      this.$http.post('/api/lesson/getLessonSpecificAvailableTime/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            this.pageView = true
            this.lessonDateDefault = res.data.availableTime || []
            this.lessonDateBooked = res.data.bookedTime || []
            if (res.data.availableTime === []) {
              this.color = 'red'
              this.errorMessages = '課程目前無可預約時段'
              setTimeout(() => {
                this.$router.push('/')
              }, 3000)
            }
          } else {
            // this.snackbar = true
            // this.color = 'red'
            // this.errorMessages = res.errMsg
            this.$router.go(-1)
          }
        })
    },
    resSelectOpenTime (val) {
      this.selectTimeList = val
    },
    resSelectOpenTimeDetail (val) {
      this.selectTimeDetailList = val
    },
    doRemoveReservationTime (val) {
      this.removeReservationTime = val
    },
    resRemoveReservationTime (val) {
      this.removeReservationTime = val
    },
    doSubmit () {
      let formData = new FormData()
      formData.append('lessonID', this.lessonId)
      formData.append('userID', this.userInfo ? this.userInfo.user_id : -1)
      formData.append('bookingDateTime', this.selectTimeList.join(';'))
      this.$http.post('/api/lesson/bookingLessons/', formData).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            this.snackbar = true
            this.color = 'green'
            this.errorMessages = '申請預約已送出'
            setTimeout(() => {
              this.$router.push('/account/finance')
            }, 3000)
          } else if (res.status === 'failed') {
            this.snackbar = true
            this.color = 'red'
            this.errorMessages = res.errMsg
          } else {
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = '發生錯誤，請稍後再試'
          }
        })
        .catch(err => {
          this.snackbar = true
          this.color = 'orange'
          this.errorMessages = '發生錯誤，請稍後再試'
          console.log(err)
        })
    }
  }
}
</script>
